import React, { useState } from 'react';
import { CNav, CNavItem, CSidebar, CSidebarNav } from '@coreui/react';
import { useDispatch, useSelector } from 'react-redux';
import { signOut } from '../../../store/auth/actions';
import { Link, useHistory } from 'react-router-dom';
import logo from './logo.png';
import { ConfirmPopup, KMSImg } from '../../../components';
import { USER_TYPES } from './constants';

type NavLinkItemType = {
  id: string;
  label: string;
  icon: string;
  to: string;
  active?: boolean;
  hide?: boolean;
  subItems?: any[];
  onClick?: any;
  disableLink?: boolean;
};

interface Props {
  location: { pathname: string; search: any };
}

/**
 * Determine nav item is link or div
 * @param props
 */
const Wrapper = (props: {
  disableLink?: boolean;
  onClick?: () => void;
  active: boolean;
  to?: string;
  children: any;
}) => {
  const { disableLink, onClick, active, to, children } = props;

  if (disableLink)
    return (
      <div className="cursor-pointer nav-link hb-navlink" onClick={onClick}>
        {children}
      </div>
    );

  return (
    <Link className={`nav-link hb-navlink${active ? ' active' : ''}`} onClick={onClick} to={to || ''}>
      {children}
    </Link>
  );
};

const Header: React.FC<Props> = (props) => {
  const { location } = props;

  const [showSidebar, setShowSidebar] = useState(false);

  const auth = useSelector((state: any) => state.auth);
  const isAdmin = auth.group_name === USER_TYPES.ADMIN;
  const history = useHistory();
  const dispatch = useDispatch();

  const [popupSignout, setPopupSignout] = useState(false);

  const openSidebar = () => setShowSidebar(true);
  const closeSidebar = () => setShowSidebar(false);

  const handleSignOut = () => {
    history.push('/admin/sign-in');
    dispatch(signOut());
  };

  let type = '';
  let status = 0;
  const { pathname, search } = location;

  if (pathname.includes('users')) {
    const query = new URLSearchParams(search);
    type = query.get('type') || USER_TYPES.ADMIN;
    status = Number(query.get('status')) || 0;
  }

  const navLinkItems: NavLinkItemType[] = [
    {
      id: '1',
      label: 'Newsfeed',
      icon: 'fas fa-book',
      to: '/admin/newsfeed',
    },
    {
      id: '2',
      label: 'Mobility',
      icon: 'fab fa-youtube',
      to: '/admin/mobility/videos',
      active: false,
      subItems: [
        {
          id: '2-1',
          label: 'Video Management',
          to: '/admin/mobility/videos',
        },
        {
          id: '2-2',
          label: 'Category Management',
          to: '/admin/mobility/categories',
        },
        {
          id: '2-3',
          label: 'Trainer Management',
          to: '/admin/mobility/trainers',
        },
      ],
    },
    {
      id: '3',
      label: 'Communication',
      icon: 'fas fa-microphone',
      to: '/admin/communication',
    },
    {
      id: '4',
      label: 'Consulting',
      icon: 'fas fa-phone-alt',
      to: '/admin/consulting/categories',
      active: false,
      subItems: [
        {
          id: '4-1',
          label: 'Category Management',
          to: '/admin/consulting/categories',
        },
        {
          id: '4-2',
          label: 'Consultation Topics Management',
          to: '/admin/consulting/diseases',
        },
      ],
    },
    {
      id: '5',
      label: 'User Management',
      icon: 'fa fa-users',
      to: '/admin/users?type=admin',
      active: false,
      subItems: [
        {
          id: '5-1',
          label: 'Admin',
          to: '/admin/users?type=admin',
          active: type === USER_TYPES.ADMIN,
        },
        {
          id: '5-2',
          label: 'Nurse',
          to: '/admin/users?type=nurse',
          active: type === USER_TYPES.NURSE && status === 0,
          subItems: [
            {
              id: '5-2-1',
              label: 'Pending Approval',
              to: '/admin/users?type=nurse&status=1',
              active: type === USER_TYPES.NURSE && status === 1,
            },
          ],
        },
        {
          id: '5-3',
          label: 'Basic Member',
          to: '/admin/users?type=user',
          active: type === USER_TYPES.USER,
        },
        {
          id: '5-4',
          label: 'Assistant',
          to: '/admin/users?type=assistant',
          active: type === USER_TYPES.ASSISTANT && status === 0,
          subItems: [
            {
              id: '5-4-1',
              label: 'Pending Approval',
              to: '/admin/users?type=assistant&status=1',
              active: type === USER_TYPES.ASSISTANT && status === 1,
            },
          ],
        },
      ],
    },
    {
      id: '6',
      label: 'Settings',
      icon: 'fa fa-cogs',
      to: '/admin/settings',
      hide: !isAdmin,
    },
    {
      id: '7',
      label: 'Sign Out',
      icon: 'fas fa-sign-out-alt',
      to: '',
      disableLink: true,
      onClick: () => setPopupSignout(true),
    },
  ];

  /**
   * Render the navlink and all the sub links by recursion
   * @param item
   */
  const renderNavLinkItem = (item: NavLinkItemType) => {
    /**
     * Click on item will close sidebar
     */
    const handleClick = () => {
      closeSidebar();
      item.onClick?.();
    };

    return (
      <div key={item.id}>
        <CNavItem className="px-3 d-compact-none c-d-minimized-none">
          <Wrapper
            active={item.disableLink ? false : item.active === undefined ? pathname.includes(item.to) : item.active}
            onClick={handleClick}
            to={item.to}
            disableLink={item.disableLink}
          >
            <i className={`hb-navlink-icon ${item.icon || 'fas fa-angle-right'}`} />
            {item.label}
          </Wrapper>
        </CNavItem>
        {!!item.subItems?.length && <div className="ml-4">{item.subItems.map((item) => renderNavLinkItem(item))}</div>}
      </div>
    );
  };

  return (
    <>
      <ConfirmPopup
        hideTitle
        centered
        btnWidth={120}
        isVisible={popupSignout}
        content="Do you want to sign out?"
        leftButtonText="No"
        rightButtonText="Yes"
        leftButtonOnPress={() => setPopupSignout(false)}
        rightButtonOnPress={handleSignOut}
      />

      <i className="fas fa-bars size-1 cursor-pointer admin-toggle-button" onClick={openSidebar} />

      <CSidebar size="xl" overlaid show={showSidebar} onShowChange={closeSidebar}>
        <div className="admin-profile" onClick={closeSidebar}>
          <KMSImg
            onClick={() => history.push('/admin/profile')}
            className="admin-avatar--img my-3 cursor-pointer"
            src={auth.avatar || logo}
            alt="Avatar"
            width="128"
            height="128"
          />
          <span className="admin-display-name">
            {auth.first_name} {auth.last_name}
          </span>
          {showSidebar && <i className="fas fa-angle-left hb-close-sidebar" />}
        </div>
        <div className="hb-admin-scroll" style={{ paddingBottom: 20 }}>
          <CNav variant="pills">
            <CSidebarNav>{navLinkItems.map((item) => renderNavLinkItem(item))}</CSidebarNav>
          </CNav>
        </div>
      </CSidebar>
    </>
  );
};

export default Header;
